import { ReactNode } from "react";
import {
	Box,
	Flex,
	Avatar,
	HStack,
	IconButton,
	Button,
	Menu,
	MenuButton,
	MenuList,
	MenuItem,
	MenuDivider,
	useDisclosure,
	useColorModeValue,
	Stack,
	Text,
} from "@chakra-ui/react";
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons";

import "./navbar.css";
import { Link } from "react-router-dom";
import { IoLogoGithub } from "react-icons/io";
export default function Simple() {
	const { isOpen, onOpen, onClose } = useDisclosure();

	return (
		<>
			<Box
				pos="sticky"
				top="0"
				left="0"
				style={{ backdropFilter: "blur(6px)" }}
				bg={"blackAlpha.900"}
				zIndex="999">
				<Flex h={16} alignItems={"center"} justifyContent={"space-between"}>
					<IconButton
						className="hamburger"
						size={"md"}
						icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
						aria-label={"Open Menu"}
						display={{ md: "none" }}
						onClick={isOpen ? onClose : onOpen}
					/>
					<HStack spacing={200} alignItems={"center"}>
						<Box>
							<Text
								className="logo"
								// bgGradient="linear(to-l,  #12c2e9, #c471ed, #f64f59)"
								// bgClip="text"
								fontSize="4xl"
								fontWeight="extrabold">
								{" SARTHAK "}
							</Text>
						</Box>
						<HStack
							as={"nav"}
							spacing={10}
							display={{ base: "none", md: "flex" }}>
							<Link
								to="/home"
								className="link"
								style={{ color: "#EEEEEE" }}
								target="_blank">
								Home
							</Link>
							<Link
								to="/home"
								className="link"
								style={{ color: "#EEEEEE" }}
								target="_blank">
								About
							</Link>
							<Link
								to="/home"
								className="link"
								style={{ color: "#EEEEEE" }}
								target="_blank">
								Contact
							</Link>
							<Link
								to="/home"
								className="link"
								style={{ color: "#EEEEEE" }}
								target="_blank">
								Project
							</Link>
							<Link
								to="/home"
								className="link"
								style={{ color: "#EEEEEE" }}
								target="_blank">
								Skills
							</Link>
						</HStack>
					</HStack>
					<Flex alignItems={"center"}></Flex>
				</Flex>

				{isOpen ? (
					<Box className="hamburger" pb={5} display={{ md: "none" }}>
						<Stack as={"nav"} spacing={4}>
							<Link
								to="/home"
								className="link"
								style={{ color: "black" }}
								target="_blank">
								About
							</Link>
							<Link
								to="/home"
								className="link"
								style={{ color: "red" }}
								target="_blank">
								About
							</Link>
						</Stack>
					</Box>
				) : null}
			</Box>
		</>
	);
}
