import {
	Flex,
	Container,
	Heading,
	Stack,
	Text,
	Button,
	Icon,
	Box,
} from "@chakra-ui/react";
import { Image } from "@chakra-ui/react";
import Typewriter from "typewriter-effect";
import "./homepage.css";
import { Divider } from "@chakra-ui/react";

export default function Homepage() {
	return (
		<div className="bg-img">
			<div className="orange-circle" />
			<Container maxW={"5xl"}>
				<Stack
					textAlign={"center"}
					align={"center"}
					spacing={{ base: 8, md: 10 }}
					py={{ base: 20, md: 28 }}>
					<Heading
						fontWeight="bold"
						fontSize={{ base: "4xl", sm: "5xl", md: "6xl" }}
						lineHeight={"130%"}
						color={"white"}>
						Hello, I am
						<Text
							bgGradient="linear(to-r, #0cffdf, #8d0eb4)"
							bgClip="text"
							fontSize={{ base: "3xl", sm: "4xl", md: "6xl" }}
							fontWeight="extrabold"
							lineHeight={"230%"}>
							Sarthak Ahuja
						</Text>
						<Text
							bgGradient="linear(to-l, #12c2e9, #c471ed, #f64f59)"
							bgClip="text"
							fontSize={{ base: "3xl", sm: "4xl", md: "6xl" }}>
							<Typewriter
								options={{
									strings: [" Full Stack Developer"],
									autoStart: true,
									loop: true,
								}}
							/>
						</Text>
					</Heading>

					<Stack spacing={6} direction={"row"}>
						<Button
							// rounded={"full"}
							px={8}
							color={"Black"}
							bg={"#12c2e9"}
							fontWeight={"bold"}
							_hover={{ bg: "orange.500" }}>
							RESUME
						</Button>
					</Stack>
				</Stack>
				<div className="orange-grid" />
				<Box></Box>
				<Divider height="100px" orientation="horizontal" />
			</Container>
		</div>
	);
}
